<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 1
      h1 Organización del espacio y ubicación de productos

    .row.justify-content-center.align-items-center
      .col-8.col-sm-6.col-lg-4.col-xl-3
        figure
          img.mb-4.mb-lg-0(data-aos="fade-down" src="@/assets/curso/temas/tema1/1.png" alt="imagen de acompañamiento")
      .col-lg-8.col-xl-7
        p La organización del espacio en el punto de venta representa un elemento fundamental en la estrategia comercial del <em>merchandising</em>, ya que influye directamente en el comportamiento del consumidor, en la experiencia de compra y en los niveles de conversión. Una disposición eficiente permite maximizar el uso del área disponible, generar un flujo lógico de circulación, destacar productos estratégicos y reforzar la imagen del establecimiento.
        .cajon.color-secundario.p-4.mb-3
          p.mb-0 Según Martínez Cruz (s. f.), el diseño del espacio debe ser funcional, estético y adaptado al perfil del consumidor, teniendo en cuenta los principios de circulación, visibilidad, accesibilidad y jerarquía de productos. El <em>layout</em>, o diseño de la planta del local, debe diseñarse para orientar al cliente a recorrer la mayor parte del espacio disponible, facilitando el contacto visual con los productos de mayor margen.
        p Entre los <em>layouts</em> más comunes se encuentran:
        ul.lista-ul--color
          li.d-flex.gap-1
            i.fa-solid.fa-check
            | 
            div
              p.mb-0 <em>Grid</em> (rejilla): óptimo para supermercados, maximiza la eficiencia operativa.
          li.d-flex.gap-1
            i.fa-solid.fa-check
            | 
            div
              p.mb-0 <em>Loop</em> o <em>racetrack</em>: guía al consumidor por un circuito cerrado.
          li.d-flex.gap-1
            i.fa-solid.fa-check
            | 
            div
              p.mb-0 <em>Layout</em> libre o mixto: usado en tiendas especializadas, favorece la exploración.

    .fullwidth-bg.fullwidth-bg--1
    .row.justify-content-center.align-items-center.z-2
      .col-xl-10
        .titulo-sexto.color-acento-contenido.mb-4
          h5 Figura 1.
          span <em>Grid</em> supertiendas 
        img.d-sm-none.mb-2(data-aos="fade-up", src='@/assets/curso/temas/tema1/2-mob.png', alt='en la figura se muestra como es el layout tipo Grid (rejilla), desarrollado en un supermercado, supentienda o tienda.')
        img.d-none.d-sm-block.mb-2(data-aos="fade-up", src='@/assets/curso/temas/tema1/2.png', alt='en la figura se muestra como es el layout tipo Grid (rejilla), desarrollado en un supermercado, supentienda o tienda.')
        figcaption <strong>Fuente:</strong> SENA, 2025.

    Separador
    .row.justify-content-center
      .col-xl-10
        #t_1_1(data-aos='zoom-in-right').titulo-segundo.color-acento-contenido.z-2
          h3 1.1	Exhibiciones primarias y secundarias

    .row.justify-content-center.align-items-center
      .col-8.col-sm-6.col-lg-4.col-xl-3
        figure
          img.mb-4.mb-lg-0(data-aos="fade-down" src="@/assets/curso/temas/tema1/3.png" alt="imagen de acompañamiento")
      .col-lg-8.col-xl-7
        p Las exhibiciones primarias son aquellas ubicaciones de alto impacto visual, situadas estratégicamente en entradas, cabeceras de góndola, vitrinas o islas. Son fundamentales para lanzar promociones, productos nuevos o destacar productos de alta rentabilidad. Estas zonas tienen alta afluencia de tránsito y permiten captar la atención del cliente desde el primer momento.
        p Por otro lado, las exhibiciones secundarias cumplen una función de refuerzo. Se ubican en pasillos interiores, estanterías laterales, puntos de pago o espacios intermedios. Aunque tienen menor visibilidad directa, permiten activar compras por impulso, complementar la exhibición primaria y estimular el recuerdo del producto.
        .p-4.rounded-4.bg-color-secundario-s10
          .row.justify-content-center.align-items-center
            .col-lg-2.d-none.d-lg-block
              figure
                img.w-60(data-aos="fade-down" src="@/assets/curso/temas/tema1/4.svg" alt="imagen de acompañamiento")
            .col-lg-10
              p.mb-0 Estas exhibiciones deben estar alineadas con el plan de <em>marketing</em> y los objetivos comerciales del punto de venta. El uso coherente de material POP, iluminación específica y señalética mejora la experiencia visual del consumidor (Editorial GG, 2016).

    Separador
    .row.justify-content-center
      .col-xl-10
        #t_1_2(data-aos='zoom-in-right').titulo-segundo.color-acento-contenido.z-2
          h3 1.2	Ubicación estratégica de productos

    .row.justify-content-center.align-items-center.z-2.mb-5
      .col-8.col-sm-6.col-lg-4.col-xl-3
        figure
          img.mb-4.mb-lg-0(data-aos="fade-down" src="@/assets/curso/temas/tema1/5.png" alt="imagen de acompañamiento")
      .col-lg-8.col-xl-7
        p La ubicación del producto dentro del punto de venta no es aleatoria. Responde a criterios técnicos definidos por el planograma, la estrategia comercial y el análisis de la rotación. Colocar productos a la altura de los ojos, por ejemplo, incrementa hasta en un 30 % la probabilidad de compra. Así mismo, los productos de alto margen suelen ubicarse en zonas calientes para asegurar su exposición prioritaria.
        .p-4.bg-color-acento-contenido-a5.rounded-4.icon-before.icon-before--termometro.mb-3
          p.mb-0 <strong>Las zonas calientes</strong>: áreas con mayor flujo de tránsito, deben utilizarse para colocar productos estratégicos como marcas propias, innovaciones o productos de rotación media. 
        .p-4.bg-color-secundario-s10.rounded-4.icon-before.icon-before--termometro-frio
          p.mb-0 <strong>Las zonas frías</strong>: espacios menos transitados, deben reactivarse mediante señalética atractiva, iluminación o reubicación de categorías.

    .fullwidth-bg.fullwidth-bg--1
    p.z-2 Se recomienda revisar los siguientes videos donde se explican los diferentes tipos de exhibición.

    .row.justify-content-center.align-items-stretch.z-2
      .col-sm-10.col-lg-6.col-xl-5.mb-3.mb-lg-0
        .p-4.bg-color-terciario-t20.rounded-4.icon-before.icon-before--video.mb-3.mb-lg-0.h-100.d-lg-flex.align-items-center.justify-content-evenly
          .d-flex.justify-content-center.align-items-center.flex-column.gap-4.flex-sm-row
            div
