                i.fa-solid.fa-link

    Separador
    #t_1_3(data-aos='zoom-in-right').titulo-segundo.color-acento-contenido.z-2
      h3 1.3	Escaparatismo

    .bloque-texto-g.color-terciario.p-3.p-sm-4.p-md-5.mb-5.z-2
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema1/6.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 El escaparatismo es una disciplina del <em>visual merchandising</em> que se encarga del diseño, planificación y montaje de los escaparates o vitrinas comerciales, con el objetivo de atraer la atención del público, comunicar mensajes de marca y estimular el deseo de compra. Va más allá de una simple exposición de productos: es una forma de comunicación visual estratégica, donde cada elemento desde la iluminación hasta la disposición de los objetos está pensado para generar un impacto emocional y sensorial en el consumidor.

    .row.justify-content-center.mb-5.z-2
      .col-xl-10
        .p-4.rounded-4.bg-color-secundario-s10
          .row.justify-content-center.align-items-center
            .col-lg-1.d-none.d-lg-block
              figure
                img.w-60(data-aos="fade-down" src="@/assets/curso/temas/tema1/7.svg" alt="imagen de acompañamiento")
            .col-lg-10
              p.mb-0 El escaparatismo no solo cumple una función estética, sino también comercial y comunicativa, ya que traduce la identidad de la marca en una experiencia visual que seduce, informa y persuade. Por esta razón, es considerado uno de los pilares más importantes del <em>merchandising</em> aplicado al punto de venta.

    .row.justify-content-center
      .col-xl-10
        h4(data-aos='zoom-in-right').mb-5
          span.etiqueta-title Componentes

      .p-4.bg-slider.rounded-3
        .tarjeta.tarjeta--blanca.p-4.py-5
          SlyderA(tipo="b")
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 Producto	
                p.mb-4.mb-lg-0 Es el elemento central de la vitrina. Debe presentarse de manera destacada, limpia y accesible visualmente. La selección de productos depende del objetivo de la campaña: puede ser promocional, de temporada o institucional.
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/23.png', alt='Imagen ilustrativa')
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 Iluminación	
                p.mb-4.mb-lg-0 La luz dirige la mirada y resalta los puntos de interés. Un sistema de iluminación adecuado permite crear profundidad, generar contrastes y acentuar las texturas o colores de los productos. La luz cálida suele transmitir confort y cercanía; la fría, modernidad y limpieza.	
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/24.png', alt='Imagen ilustrativa')
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 Color
                p.mb-4.mb-lg-0 El color tiene un papel psicológico fundamental. Aporta identidad, comunica emociones y establece coherencia con la imagen de marca. Además, ayuda a jerarquizar los elementos dentro del escaparate, guiando la atención del observador.
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/25.png', alt='Imagen ilustrativa')
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 Composición y equilibrio
                p.mb-4.mb-lg-0 La forma en que se distribuyen los elementos determina la armonía visual. Se deben cuidar las proporciones, la simetría y la dirección de la mirada. Una buena composición combina creatividad con funcionalidad, garantizando que el mensaje visual sea claro y efectivo.
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/26.png', alt='Imagen ilustrativa')
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 Los materiales y soportes
                p.mb-4.mb-lg-0 Incluyen estructuras, maniquíes, bases, pedestales, fondos decorativos y elementos de ambientación. Su elección debe garantizar resistencia, funcionalidad y armonía con la propuesta visual.
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/27.png', alt='Imagen ilustrativa')
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 El movimiento y la tecnología	
                p.mb-4.mb-lg-0 El uso de recursos dinámicos, como pantallas digitales, efectos de luz, sonido o movimiento mecánico, aporta innovación y aumenta la capacidad de atracción del escaparate.
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/28.png', alt='Imagen ilustrativa')
            .row.align-items-center.justify-content-evenly
              .col-sm-10.col-lg-5.col-xl-4
                h5.mb-4 El mensaje y la comunicación visual
                p.mb-4.mb-lg-0 Todo escaparate transmite un mensaje, explícito o implícito. Este debe ser claro, coherente con la identidad de marca y comprensible para el público. Los textos, logotipos y elementos gráficos deben integrarse de forma equilibrada.
              .col-sm-10.col-lg-6.col-xl-6
                figure
                  img.mb-4.mb-lg-0(src='@/assets/curso/temas/tema1/29.png', alt='Imagen ilustrativa')
                  

    Separador
    #t_1_4(data-aos='zoom-in-right').titulo-segundo.color-acento-contenido.z-2
      h3 1.4	Planogramas y categorías

    img.z-2(data-aos="fade-up")(src="@/assets/curso/temas/tema1/30.png", alt="Imagen decorativa")

    .row.justify-content-center.z-2.mb-5
      .col-lg-10(data-aos="fade-down")
        div.banner-text.bg-color-secundario-s20
          p.mb-0 Un planograma es un esquema visual que muestra cómo deben organizarse los productos en los estantes o exhibidores de un punto de venta para optimizar el espacio, facilitar la compra y aumentar las ventas. Su elaboración implica analizar el comportamiento del consumidor, la rotación de productos y la estrategia comercial del establecimiento. Se diseña teniendo en cuenta aspectos como la altura de los anaqueles, la visibilidad del producto, las zonas calientes y frías, y las categorías de artículos, buscando siempre una presentación atractiva, ordenada y funcional (Levy & Weitz, 2017).

    .row.justify-content-center
      .col-xl-10
        h4(data-aos='zoom-in-right').mb-5
          span.etiqueta-title Proceso eficaz de creación de planogramas

    .row.justify-content-center.mb-5
      .col-xl-10
        TabsA.color-primario
          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Recopilación de datos de SKU")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Recopilación de datos de SKU
                p.mb-4 Se recolectan dimensiones y fotos de cada producto del inventario.
                p.mb-4 <strong>Objetivo principal</strong>: Garantizar precisión y uso eficiente del espacio.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/31.png")

          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Registro de equipos y estanterías")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Registro de equipos y estanterías
                p.mb-4 Se registran los tipos y tamaños de estanterías y accesorios en la tienda.
                p.mb-4 <strong>Objetivo principal</strong>: Asegurar compatibilidad del planograma con la infraestructura.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/8.png")

          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Creación del CDT (categoría / cliente)	")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Creación del CDT (categoría / cliente)	
                p.mb-4 Se analiza el perfil del cliente y su comportamiento por categoría.
                p.mb-4 <strong>Objetivo principal</strong>: Mejorar la ubicación del producto según preferencias del cliente.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/9.png")

          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Diseño del planograma con <em>software</em>")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Diseño del planograma con <em>software</em>
                p.mb-4 Se usa <em>software</em> especializado y se valida con gestores de categoría.
                p.mb-4 <strong>Objetivo principal</strong>: Alinear el diseño con metas comerciales.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/10.png")

          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Formación del personal en <em>visual merchandising</em>")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Formación del personal en <em>visual merchandising</em>
                p.mb-4 Se capacita al equipo sobre el uso del <em>software</em> y normas de exhibición.
                p.mb-4 <strong>Objetivo principal</strong>: Asegurar ejecución coherente en tiendas.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/11.png")

          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Revisión del rendimiento del planograma")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Revisión del rendimiento del planograma
                p.mb-4 Se analiza el desempeño del planograma usando datos de ventas y métricas.
                p.mb-4 <strong>Objetivo principal</strong>: Realizar ajustes estratégicos basados en resultados.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/12.png")

          .tarjeta.bg-color-primario-p10.p-4.pt-5.h-100.d-flex.align-items-center(titulo="Plan de comunicación e integración")
            .row.align-items-center.justify-content-around
              .col-12.col-sm-7
                h5.mb-4 Plan de comunicación e integración
                p.mb-4 Se coordina la implementación del planograma entre departamentos.
                p.mb-4 <strong>Objetivo principal</strong>: Facilitar ejecución y actualización efectiva.
              .col-4.col-sm-3
                figure
                  img(src="@/assets/curso/temas/tema1/13.png")

    .row.justify-content-center.align-items-center.z-2.mb-5
      .col-xl-10
        .titulo-sexto.color-acento-contenido.mb-4
          h5 Figura 2.
