        img.d-none.d-sm-block.mb-2(data-aos="fade-up", src='@/assets/curso/temas/tema1/15.png', alt='La figura muestra como debe ser la ubicación de los productos en un estante según altura entre 30 cms y 60 cms.')
        figcaption <strong>Fuente:</strong> SENA, 2025.

