            p.mb-4.mb-5.text-center 62 % de consumidores prefieren tiendas con esta característica.

          .tarjeta.bg-color-gray.px-4.pt-5.factores-slider__tarjeta
            .row.justify-content-center.mb-3
              .col-7
                img.factores-slider__icon(src='@/assets/curso/temas/tema1/21.png' alt='Ilustacion de acompañamiento')
            .factores-slider__title-wrapper  
              h4.factores-slider__title.mb-4 Operaciones racionalizadas
            p.mb-4.text-center Mejora de productividad hasta un 20 %.

          .tarjeta.bg-color-gray.px-4.pt-5.factores-slider__tarjeta
            .row.justify-content-center.mb-3
              .col-7
                img.factores-slider__icon(src='@/assets/curso/temas/tema1/22.png' alt='Ilustacion de acompañamiento')
